import React, { useState, useEffect } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import Routes from './components/Routes';
import Login from './components/Login';
export default function App() {

  const [loggedIn, setLoggedIn] = useState(false);
  const [currentUser, setCurrentUser] = useState();
  const [currentComponent, setCurrent] = useState('Login');

  useEffect(() => {
    getInitialState()
  }, []);

  window.addEventListener('beforeunload', (event) => {
    // Cancel the event as stated by the standard.
    event.preventDefault();
    
    // Chrome requires returnValue to be set.
      setLocalStorage();
    });

  function setLocalStorage() { 
      if (currentComponent === 'Login') {
        localStorage.setItem('currentUser', JSON.stringify(''));
      } else {
        localStorage.setItem('currentUser', JSON.stringify(currentUser));
      }
  }

  function getInitialState() {
      let storage = localStorage.getItem('currentUser');
      console.log(storage);
      console.log(typeof storage);
      if (storage !== 'undefined' && storage) {
        console.log("firing");
        let user = JSON.parse(storage);
        setCurrentUser(user);
        setLoggedIn(user.login)
      }
      
  }

  function clearLocalStorage() {
    localStorage.clear();
  }

  function Logout() {
    setCurrent('Login')
    setLoggedIn(false);
    localStorage.setItem('currentUser')
    window.removeEventListener("beforeunload", clearLocalStorage());
    
  
}

  function userLogin(user) {
    setCurrent('Routes');
    setCurrentUser(user);
    setLoggedIn(user.login);
  }

  function setRoutes() {
    setCurrent('Routes');
  }
  // if (loggedIn) {
      return (
          // <Routes setCurrentComponent={() => setRoutes()} currentUser={currentUser} logout={() => Logout()} />
          <Routes setCurrentComponent={() => setRoutes()} />
      );
  // } else {
  //   return (
  //     <Login userLogin={(user) => userLogin(user)} />
  //   );
  // }
}


