import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import { Modal } from 'react-bootstrap';
import * as Constants from './constants';
import axios from 'axios';
import Header from './Header';
import Dashboard from './Dashboard';
import Drinks from "./Drinks";


export default function Routes(props) {
    const [showNotify, setShowNotify] = useState(false);
    const [notifications, setNotifications] = useState([]);

    useEffect(() => {
        props.setCurrentComponent();
      }, []);



    function viewNotification(id) {
        let data = {
            id: id
        }
        data = JSON.stringify(data);
        axios.post(Constants.URL + `?action=viewNotify`, data)
            .then(res => {
                // getNotifications();
            })
    }

    function handleLogout() {
        props.logout();
      }

    function toggleSetNotify() {
        setShowNotify(!showNotify);
    }

    function hideModal() {
        setShowNotify(false);
        //props.toggleShowNotify();
    }

    function notifySideBar() {
        let sideBar = [];
        if (showNotify) {
            sideBar.push(
                    <label className="mainFocus" onClick={() => toggleSetNotify()} for="btnControl"></label>
            );
            if (notifications.length > 0) {
                sideBar.push(
                    <div className="notifySide" style={{backgroundColor: "whitesmoke"}}> 
                        {listNotify()}
                    </div>
                );
            } else {
                sideBar.push(
                    <div className="notifySide" style={{backgroundColor: "whitesmoke"}}> 
                        <p>No Notifications</p>
                    </div>
                    
                )
            }
        } else {
            sideBar = [];
        }
        
        return sideBar;
    }
    function listNotify() {
        let notif = [];
        for(let i = 0; i < notifications.length; i++) {
            if (!notifications[i].description.includes(props.currentUser.name)) {
                notif.push(
                    <div onClick={() => viewNotification(notifications[i].id)} style={{borderBottom: "1px groove"}}>
                        <p>{notifications[i].description}</p>
                    </div>
                )
            }
        }
        return notif;
    }

    function countNotify() {
        let count = 0;
        for(let i = 0; i < notifications.length; i++) {
            if (!notifications[i].description.includes(props.currentUser.name)) {
                count++;
            }
        }
        return count
    }
      
    return (
        <div>
            <div>
                <Router>
                    <Header countNotify={countNotify()} showNotify={showNotify} toggleShowNotify={() => toggleSetNotify()} currentUser={props.currentUser} handleLogout={() => handleLogout()} />
                    <div>
                        {notifySideBar()}
                    </div>
                    <Switch style={{zIndex: "9"}}>
                        <Route path="/logout">
                            <Redirect to="/" />
                        </Route>
                        <Route path="/drinks">
                            <Drinks />
                        </Route>
                        <Route path="/">
                            <Dashboard showNotify={showNotify} toggleShowNotify={() => toggleSetNotify()} currentUser={props.currentUser} />
                        </Route>
                    </Switch>
                </Router>
            </div>    
        </div>
    )
}