
import React, { useState, useEffect } from 'react'
import { Navbar, Nav } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'
import { BsBellFill } from 'react-icons/bs';
import Sidebar from './Sidebar';
import '../styles/Sidebar.css';
import { Route } from 'react-router-dom';
import Routes from './Routes';


export default function Header(props) {
    const [count, setCount] = useState(0);

    useEffect(() => {
        setCount(props.countNotify)
    }, [props.countNotify]);

    function indicateNotify() {
        let style = {
            fontSize: "14px",
            color: "white"
        }
        let text = count;
        if (count == 0) {
            text = "";
        }
        return <p style={style}><b>{text}</b></p>;
    }

    let bellStyle = { color: "red", marginTop: "4px", marginLeft: "-7px", fontSize: "32px" };
    if (count < 1) {
        bellStyle = { color: "white", marginTop: "4px", marginLeft: "-7px", fontSize: "32px" };
    }

    function clickIcon() {
        props.toggleShowNotify();
    }

    return (
        <div style={{ display: "flex", width: "100%", backgroundColor: "#3490dc" }}>
            <div style={{width: "30%", display: "flex", textAlign: "center" }}>
                <Sidebar />
            </div>
            <div id={"ctcTitle"} style={{width: "100%", display: "flex", textAlign: "center", paddingBottom: "5px" }}>
                <div style={{ width: "80%" }}>
                    <h4 style={{ margin: "auto", marginRight: "12%", color: "whitesmoke", overflow: "visible" }}>My Bar</h4>
                    <h6 style={{ margin: "auto", marginRight: "10%", color: "whitesmoke", overflow: "visible" }}><b>Current User: </b>bill</h6>
                </div>
                <div style={{marginLeft: "auto" }}>
                    <div style={{ marginTop: "7px", display: "flex", textAlign: "center" }} >
                        <LinkContainer to="/logout" className={"active navbar-brand"} style={{color: "whitesmoke"}}>
                            <Nav.Link id="logoutButton" onClick={props.handleLogout}>Logout</Nav.Link>
                        </LinkContainer>
                    </div>
                </div>
                <div >
                    <div style={{ marginTop: "7px", display: "flex", textAlign: "center", marginRight: "2rem", marginLeft: "-5px" }} onClick={() => clickIcon()}>
                        <BsBellFill style={bellStyle} />
                        <div style={{ cursor: "pointer", width: "30px", marginLeft: "-6px", marginTop: "-8px", position: "absolute" }}>
                            {indicateNotify()}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
} 