import React, { useEffect, useState } from 'react';
import {Container, Row, Col, Button, Modal} from 'react-bootstrap';
import * as Constants from './constants';
import axios from 'axios';
import ActivityType from './ActivityType';
import RecentItem from './RecentItem';
export default function Dashboard(props) {
    const [updates, setUpdates] = useState();
    const [showNotify, setShowNotify] = useState(false);
    const [showModal, setShowModal] = useState(false);
    const [details, setDetails] = useState("");
    useEffect(() => {
        getRecentActivity();
    }, []);

    useEffect(() => {
        setShowNotify(props.showNotify)
    }, [props.showNotify]);

    function getRecentActivity() {
        axios.get(Constants.URL+`?action=recentUpdates`)
            .then(res => {
                console.log(res.data);
                setUpdates(res.data);
            })
    }

    function showRecentRollouts() {
        let allUpdates = [];
        if (updates) {
            for(let i = 0; i < updates.length; i++) {
                allUpdates.push(
                    <Col sm><RecentItem update={updates[i]} seeDetails={(d) => seeDetails(d)} /></Col>
                )
            }
        }
        
        return allUpdates;
    }

    function seeDetails(d) {
        if (d) {
            setDetails(d);
            setShowModal(true);
        } else {
            setDetails("No Version Details Provided.");
            setShowModal(true);
        }
    }

    function descriptModal() {
        let modal = [];
        modal.push(
            <Modal size="lg" show={showModal} onHide={() => setShowModal(false)} style={{textAlign: "center"}}>
                <div>
                    <p style={{whiteSpace: "pre-line"}}>{details}</p>
                </div>
            </Modal>
        );

        return modal;
    }

    // let logsButton = [];
    // if (props.currentUser.super_user) {
    //     logsButton.push(
    //         <Button variant="primary" style={{float: "right", backgroundColor: "#3490dc", height: "35px", fontSize: "14px"}}>
    //             View Logs
    //         </Button>
    //     )
    // }

    return (
        <div>
           <h3>Bar Front Page</h3>
        </div>
    );
}