import React, { useState, useEffect, version } from 'react';
import axios from 'axios';
import SearchField from 'react-search-field';

import {
    Modal,
    Table,
    Card,
    Container,
    Row,
    Col,
    Accordion
} from 'react-bootstrap';
import * as Constants from './constants';
import '../styles/logStyles.css';
import { BiLoader } from "react-icons/bi";
import ToggleButton from './ToggleButton'
import { useHistory, Link } from 'react-router-dom';

export default function Drinks() {
    const [drinks, setContainers] = useState();
    const [containerHistory, setContainerHistory] = useState();
    const [showResult, setShowResult] = useState(false);
    const [search, setSearch] = useState('');
    const [selectedFacilityTitle, setSelectedFacilityTitle] = useState('');
    const [flaggedOnly, setFlaggedOnly] = useState(false);
    const onChangeSearch = query => setSearch(query);
    const history = useHistory();

    useEffect(() => {
        getContainers();
        let timer = setInterval(() => getContainers(), 60000);
        return function cleanup() {
            clearInterval(timer);
        }
    }, []);

    // <editor-fold defaultstate="collapsed" desc="Population Methods">
    //Populates all facilities and drinks
    function containerRows() {
        let allContainers = [];
        let facilitiesDown = isFacilityAlert();
        for (let x = 0; x < drinks.length; x++) {
            let rowData = rowDataFunc(drinks[x]);
            let displayFacility = drinks[x]["facility_info"]["facility_name"] + ' - ' + drinks[x]["facility_info"]["facility_id"];
            let style = { backgroundColor: Constants.custWhite, cursor: "pointer" };

            if (flaggedOnly && !facilitiesDown[x]) { 
                continue; 
            }

            if (facilitiesDown[x]) {
                style = { backgroundColor: Constants.custRed, cursor: "pointer" }
            }
            allContainers.push(
                <Card>
                    <Card.Header>
                        <div style={style}>
                            <Accordion.Toggle as={Card.Header} variant={"Button"} eventKey={x + 1}>
                                {displayFacility}
                            </Accordion.Toggle>
                        </div>
                        <Accordion.Collapse eventKey={x + 1}>
                            <Card.Body>
                                <Table responsive>
                                    <tbody>
                                        <tr>
                                            <th>Name</th>
                                            <th>Version</th>
                                            {/*<th>Running</th>*/}
                                            <th>Created</th>
                                        </tr>
                                        {rowData}
                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Accordion.Collapse>
                    </Card.Header>
                </Card>
            );
        }
        return allContainers;
    }

    //Populates specified facility and container(s)
    function containerRowsSpecific() {
        let allContainers = [];
        let gotSearchResults = false;
        let facilitiesDown = isFacilityAlert();
        for (let x = 0; x < drinks.length; x++) {
            let displayFacility = drinks[x]["facility_info"]["facility_name"] + ' - ' + drinks[x]["facility_info"]["facility_id"];
            let style = { backgroundColor: Constants.custWhite, cursor: "pointer" };
            let active = 1;
            let rowData = rowDataFunc(drinks[x]);

            if (selectedFacilityTitle !== "" && selectedFacilityTitle !== displayFacility) {
                //one result, active stays as 1
                continue;
            }

            if (flaggedOnly && !facilitiesDown[x]) { 
                continue; 
            }

            if (facilitiesDown[x]) {
                style = { backgroundColor: Constants.custRed, cursor: "pointer" }
            }

            if (search !== "") {
                let oneDimArr = [];
                for (let i = 0; i < Object.values(drinks[x]).length - 1; i++) {
                    oneDimArr = oneDimArr.concat(drinks[x][i]);
                }

                let finder = filterList(search, oneDimArr)
                if (finder.length > 0 && !finder.includes(null) && !finder.includes(undefined)) {
                    rowData = rowDataSpecificFunc(drinks[x], finder);
                    gotSearchResults = true;
                    //active == 1 ? active = 1 : active++;
                } else {
                    //active == 1 ? active = 1 : active--;
                    continue;
                }
            }

            allContainers.push(
                <Accordion defaultActiveKey={active} >
                    <Card>
                        <Card.Header>
                            <div style={style}>
                                <Accordion.Toggle as={Card.Header} variant={"Button"} eventKey={active}>
                                    {displayFacility}
                                </Accordion.Toggle>
                            </div>
                            <Accordion.Collapse eventKey={active}>
                                <Card.Body>
                                    <Table responsive>
                                        <tbody id={x + 1}>
                                            <tr>
                                                <th>Name</th>
                                                <th>Version</th>
                                                {/*<th>Running</th>*/}
                                                <th>Created</th>
                                            </tr>
                                            {rowData}
                                        </tbody>
                                    </Table>
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card.Header>
                    </Card>
                </Accordion>
            );
        }
        if (!gotSearchResults && search !== "") {
            allContainers.push("No Results Found")
        }
        return allContainers;
    }

    //</editor-fold>

    // <editor-fold defaultstate="collapsed" desc="Helper Methods">

    //Returns multi-dimension array as Drinks[Container[
    // Item0["test", "1.2", "false", "2 days ago"], Item0["test2", "1.3", "true", "3 days ago"],
    // facility_info["facility_id":"3186291", "facility_name":"zCTCFacility"]]]
    function getContainers() {
        axios.get(Constants.URL + `?action=viewAllContainers`)
            .then(res => {
                console.log(res);
                setContainers(res.data);
            })
    }

    function getContainerHistory(facilityID, container) {
        let data = { facilityID: facilityID, containerID: container[4] }
        axios.get(Constants.URL + `?action=getContainerHistory&data=` + JSON.stringify(data))
            .then(res => {
                console.log(res);
                res.data[0][4] = container[0]
                res.data[0][5] = container[1]
                console.log("here: ")
                console.log(res.data)
                setContainerHistory(res.data)
            })
    }

    function rowDataFunc(container) {
        let rowData = [];
        for (let x = 0; x < Object.values(container).length - 1; x++) {
            pushRowData(container, x, rowData);
        }
        return rowData;
    }

    function rowDataSpecificFunc(container, data) {
        let rowData = [];
        for (let x = 0; x < Object.values(container).length - 1; x++) {
            if (!findCommonElements2(data, container[x])) {
                continue;
            }
            pushRowData(container, x, rowData);
        }
        return rowData;
    }

    function pushRowData(container, x, rowData) {

        let style = { cursor: "pointer" };
        if (container[x][2] === "f") {
            style = { backgroundColor: Constants.custRed, cursor: "pointer" };
        }

        rowData.push(
            <tr style={style} onClick={() => {
                setShowResult(true);
                getContainerHistory(container["facility_info"]["facility_id"], container[x]);
                drawHistoryModal()
            }}>
                <td>{container[x][0]}</td>
                <td>{container[x][1]}</td>
                {/*<td>{container[x][2]}</td>*/}
                <td>{container[x][3]}</td>
            </tr>
        )
        return rowData
    }

    function isFacilityAlert() {
        let isFacility = [];
        for (let i = 0; i < drinks.length; i++) {
            let down = false;
            for (let k = 0; k < Object.values(drinks[i]).length - 1; k++) {
                if (drinks[i][k][2] === 'f') {
                    down = true;
                }
            }
            isFacility.push(down);
        }
        return isFacility;
    }

    //Search Bar Algo
    function filterList(q, list) {
        function escapeRegExp(s) {
            return s.replace(/[-/\\^$*+?.()|[\]{}]/g, "\\$&");
        }

        const words = q
            .split(/\s+/g)
            .map(s => s.trim())
            .filter(s => !!s);

        const hasTrailingSpace = q.endsWith(" ");
        const searchRegex = new RegExp(
            words
                .map((word, i) => {
                    if (i + 1 == words.length && !hasTrailingSpace) {
                        // The last word - ok with the word being "startswith"-like
                        return `(?=.*\\b\\w*${escapeRegExp(word)})`;
                    } else {
                        // Not the last word - expect the whole word exactly
                        return `(?=.*\\b\\w*${escapeRegExp(word)}\\b)`;
                    }
                })
                .join("") + ".+",
            "gi"
        );
        return list.filter(item => {
            return searchRegex.test(item);
        });

    }

    function findCommonElements2(arr1, arr2) {
        // Create an empty object
        let obj = {};

        // Loop through the first array
        for (let i = 0; i < arr1.length; i++) {

            // Check if element from first array
            // already exist in object or not
            if (!obj[arr1[i]]) {

                // If it doesnt exist assign the
                // properties equals to the
                // elements in the array
                const element = arr1[i];
                obj[element] = true;
            }
        }

        // Loop through the second array
        for (let j = 0; j < arr2.length; j++) {

            // Check elements from second array exist
            // in the created object or not
            if (obj[arr2[j]]) {
                return true;
            }
        }
        return false;
    }

    function findDiff(jsonContainer) {
        let jsonObject1 = JSON.parse(jsonContainer[2]);
        let jsonObject2 = JSON.parse(jsonContainer[3]);

        if (jsonObject1 == null) { //INSERT
            return (checkForNull(jsonContainer[4]) + " (Version: " + checkForNull(jsonContainer[5]) + ")" +
                "\nRunning = " + checkForNull(jsonObject2["running"]));
        }
        if (jsonObject2 == null) { //DELETE
            return (checkForNull(jsonContainer[4]) + " (Version: " + checkForNull(jsonContainer[5]) + ")" +
                "\nRunning = " + checkForNull(jsonObject2["running"]));
        }
        let keys = Object.keys(jsonObject1);
        for (let i = 0; i < keys.length; i++) { //UPDATE
            let key = keys[i];
            if (jsonObject1[key] !== jsonObject2[key]) {
                if (jsonObject1[key] == true && jsonObject2[key] == false) {
                    return ("Container Stopped Running");
                }
                else if (jsonObject1[key] == false && jsonObject2[key] == true) {
                    return ("Container Started Running");
                }
                return (key + " changed from " + checkForNull(jsonObject1[key]) + " to " + checkForNull(jsonObject2[key]));
            }
        }
    }

    function checkForNull(obj) {
        if (obj == null) {
            return "N/A"
        }
        else { return JSON.stringify(obj) }
    }

    //Dynamically fill the drop down menu
    function createMenuOptions() {
        let optionsArr = [];
        optionsArr.push(
            <option value={""}>
                Not Set
            </option>
        );
        for (let x = 0; x < drinks.length; x++) {
            optionsArr.push(
                <option
                    value={drinks[x]["facility_info"]["facility_name"] + ' - ' + drinks[x]["facility_info"]["facility_id"]}>
                    {drinks[x]["facility_info"]["facility_name"] + ' - ' + drinks[x]["facility_info"]["facility_id"]}
                </option>
            );
        }
        return optionsArr
    }

    function handleCloseResult() {
        setShowResult(false);
    }

    function drawHistoryModal() {
        let modal = [];

        modal.push(
            <Modal show={showResult} onHide={() => handleCloseResult()}>
                <Modal.Header closeButton>
                    <Modal.Title>Container History</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div>
                        <Table style={{ margin: "auto", width: "90%" }}>
                            <thead>
                                <tr style={{ backgroundColor: "#3490dc", padding: "5rem" }}>
                                    <th style={{ color: "#fff" }}><p>Event</p></th>
                                    <th style={{ color: "#fff", }}><p>Action</p></th>
                                    <th style={{ color: "#fff", }}><p>Date</p></th>
                                </tr>
                            </thead>
                            <tbody>
                                {pushModalData()}
                            </tbody>
                        </Table>
                    </div>
                </Modal.Body>
            </Modal>
        );

        return modal;
    }



    function pushModalData() {
        if (containerHistory == null || Object.values(containerHistory).length <= 0) {
            return (<tr>No History</tr>);
        }
        let contents = [];

        for (let i = 0; i < Object.values(containerHistory).length; i++) {
            let event = containerHistory[i][1];
            if (containerHistory[i][1] === "INSERT") {
                event = "ADDED"
            }
            if (containerHistory[i][1] === "DELETE") {
                event = "REMOVED"
            }
            contents.push(
                <tr key={i}>
                    <td>{event}</td>
                    <td style={{ overflowWrap: "break-word" }}>{findDiff(containerHistory[i])}</td>
                    <td>{containerHistory[i][0].substring(0, containerHistory[i][0].indexOf("."))}</td>
                </tr>
            );
        }
        return contents
    }

    function handleTaskView(url) {
        console.log(url);
        console.log(history);
        history.push(url);
    }

    // </editor-fold>

    if (drinks && search === "" && selectedFacilityTitle === "") {
        return (
            <Container>
                <Row>
                    <Col lg={"5"}>
                        <h3>Container Status</h3>
                    </Col>
                </Row>
                <Row>
                    <Col md={"8"}>
                        <div style={{width: "fit-content", float: "left"}}>
                        <label>
                            Flagged Only
                        </label>
                        <br />
                        <ToggleButton onChange={(e) => setFlaggedOnly(e)} />
                        </div>
                        <SearchField
                            placeholder="Search"
                            value={search}
                            onChange={onChangeSearch}
                        />
                    </Col>
                    
                    <Col md={"1"}>
                    </Col>

                    <Col md={"3"}>
                        <select onChange={(e) => setSelectedFacilityTitle(e.target.value)}
                            name="FacilityID" value={selectedFacilityTitle}>
                            {createMenuOptions()}
                        </select>
                    </Col>
                </Row>
                <br />
                <Row>
                    <Col lg={"12"}>
                        <Accordion defaultActiveKey={0}>
                            {containerRows()}
                            {drawHistoryModal()}
                        </Accordion>
                    </Col>
                </Row>
            </Container>
        )
    } else if (drinks && (search !== "" || selectedFacilityTitle !== "")) {
        return (
            <Container>
            <Row>
                <Col lg={"5"}>
                    <h3>Container Status</h3>
                </Col>
            </Row>
            <Row>
                <Col md={"8"}>
                    <div style={{width: "fit-content", float: "left"}}>
                    <label>
                        Flagged Only
                    </label>
                    <br />
                    <ToggleButton onChange={(e) => setFlaggedOnly(e)} />
                    </div>
                    <SearchField
                        placeholder="Search"
                        value={search}
                        onChange={onChangeSearch}
                    />
                </Col>
                
                <Col md={"1"}>
                </Col>

                <Col md={"3"}>
                    <select onChange={(e) => setSelectedFacilityTitle(e.target.value)}
                        name="FacilityID" value={selectedFacilityTitle}>
                        {createMenuOptions()}
                    </select>
                </Col>
            </Row>
            <br />
            <Row>
                <Col lg={"12"}>
                    <Accordion defaultActiveKey={0}>
                        {containerRowsSpecific()}
                        {drawHistoryModal()}
                    </Accordion>
                </Col>
            </Row>
        </Container>
        )
    } else {
        return (
            <Container>
                <div id="loadingDiv" style={{ textAlign: "center", marginTop: "15%" }}>
                    <BiLoader size="250px" color="3498FF" />
                </div>
            </Container>
        );
    }
}

